<template>

<div>
    <header class="grid col-full">
        <hr>
        <p class="fleft">Blog / 博文</p>
    </header>
    <!-- 正文部分 -->
    <section class="grid col-three-quarters mq2-col-two-thirds mq3-col-full">
      <!-- 博文列表 -->
      <article class="post" v-for="item in blogData" :key="item._id">
				<h2><a href="javascript:;" @click="blogTap(item._id)" class="post-title" v-text="item.title"></a></h2>
				<div class="meta">
					<p>Posted on <span class="time">November 15, 2011</span> by <a href="#" class="fn">Sylvain Lafitte</a> in <a href="#" class="cat">Other</a> with <a href="#" class="comments-link">42 comments</a>.</p>
				</div>
				<div class="entry">
					<p v-text="item.intro">文章内容</p>
				</div>
				<footer>
					<a href="javascript:;" @click="blogTap(item._id)" class="more-link">Continue reading…</a>
				</footer>
			</article>
			<ul class="page-numbers" v-if="blogData.length > 5">
				<li><a href="#">Prev</a></li>
				<li><a href="#" class="current">1</a></li>
				<li><a href="#">2</a></li>
				<li><a href="#">3</a></li>
				<li><a href="#">4</a></li>
				<li><a href="#">Next</a></li>
			</ul>
		</section>
</div>

</template>
<script>
export default {
  data: function () {
    return {
      img_port: ''
    }
  },
  props: {
    blogData: {
      type: Array
    }
  },
  methods:{
    // 再次向上传递，传递到父组件(触发路由改变)
    blogTap: function(id){
      this.$emit('blogTap',id);
    }
  }
}
</script>



