<template>
  <div class="work-page main grid-wrap">

		<header class="grid col-full">
			<hr>
      <router-link :to="{ name: 'portfolioList'}"><p class="fleft">article / 作品集</p></router-link>

		</header>

		<aside class="grid col-one-quarter mq2-col-one-third mq3-col-full">
      <h6>文章标题</h6>
			<p class="mbottom" v-text="articleData.team_name">
			</p>
			<h6>团队名称</h6>
			<p class="halfmbottom" v-text="articleData.team_name"></p>

			<h6>简介</h6>
			<ul class="halfmbottom">
				<li v-text="articleData.intro"></li>
			</ul>

			<h6>Technology</h6>
			<ul class="halfmbottom">
				<li v-text="articleData.technology"></li>
			</ul>

			<h6>Links</h6>
			<ul class="mbottom">
				<!-- <li><a href="http://">Live website</a> - http://www.??.com</li> -->
				<li v-text="articleData.links"></li>
			</ul>

			<h6>Testimonial</h6>
			<blockquote v-text="articleData.testimonial"></blockquote>

		</aside>

		<section class="grid col-three-quarters mq2-col-two-thirds mq3-col-full">

		<!-- <figure class="">
			<a href="#" >
			<img src="img/img.jpg" alt="" >
			</a>
			<figcaption>
				<p>Lorem ipsum dolor set amet</p>
			</figcaption>
		</figure> -->

		<figure class="" v-html="articleData.content"></figure>

		</section>
	</div> <!--main-->
</template>

<script>
export default {
  name: "portfolio-detail",
  data: function () {
    return {
      articleData: ''
    }
  },
  mounted: function(){
    this.articleData = this.$route.params.articleData;
    //如果没有数据 返回列表页 防止用户在当前页面刷新时，无数据报错
    if(!this.articleData){
      this.$router.push({ name: 'portfolioList'})
    }
  },
  computed:{
    articleContent:function () {

    }
  }
}
</script>



